.nav-bar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #e7df6f;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s;
}

.nav-bar ul {
  list-style-type: none;
}

.nav-link {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #535252;
  display: block;
  transition: 0.3s;
}

.nav-link:hover {
  color: #9e9696;
}

.open-btn {
  font-size: 30px;
}

.open-btn:hover {
  cursor: pointer;
  border-bottom: 2px solid rgb(231, 229, 103);
}

.close-btn:hover {
  cursor: pointer;
  border-bottom: 2px solid rgb(231, 229, 103);
}

.nav-bar .close-btn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

@media screen and (max-height: 450px) {
  .nav-bar {
    padding-top: 15px;
  }
  .nav-link {
    font-size: 18px;
  }
}
